import "./App.css";
import React, { useState, useReducer } from "react";
import { App, Button, Layout, Menu } from "antd";
import SiderBox from "./Sider";
import ContentBox from "./Content";
import reducer from "./reducer";

function AppIndex() {
  const initialState = {
    selectedKeys: "dandelion", //当前种类
  };
  const [state, dispatch] = useReducer(reducer, initialState);
  return (
    <App>
      <Layout style={{ minHeight: "100vh" }}>
        <SiderBox dispatch={dispatch} selectedKeys={state.selectedKeys}/>
        <ContentBox dispatch={dispatch} selectedKeys={state.selectedKeys} />
      </Layout>
    </App>
  );
}

export default AppIndex;
