import { App, Button, Layout, Menu } from "antd";
import React, { useState } from "react";
import Icon, { MailOutlined, SettingOutlined } from "@ant-design/icons";
import YellowCress from "./assets/images/huangancai.jpeg";
import Dandelion from "./assets/images/pugongying.jpeg";

const { Sider } = Layout;

const items = [
  {
    key: "dandelion",
    icon: <img src={Dandelion} className="tabIcon" />,
    label: "蒲公英",
    title: "蒲公英",
  },
  {
    key: "yellowCress",
    icon: <img src={YellowCress} className="tabIcon" />,
    label: "黄鹌菜",
    title: "黄鹌菜",
  },
];

const SiderBox = ({ dispatch, selectedKeys }) => {
  const [collapsed, setCollapsed] = useState(false); //侧边栏收缩
  return (
    <Sider collapsible collapsed={collapsed} onCollapse={(value) => setCollapsed(value)}>
      <Menu theme="dark" mode="inline" items={items} selectedKeys={selectedKeys} onSelect={(info) => {
        dispatch({
            type: 'changeSelectedKeys',
            payload: info.key
        })
      }}></Menu>
    </Sider>
  );
};

export default SiderBox;
